<template>
  <div class="autocomplete">
    <input
      class="search-input"
      type="text"
      v-model="search"
      @input="onChange"
    />
    <ul
        v-show="isOpen"
        class="autocomplete-results"
    >
      <li
        v-for="(result, i) in results"
        :key="i"
        @click="setResult(result)"
        class="autocomplete-result"
      >
        {{ result }}
      </li>
    </ul>
  </div>
</template>

<style>
  .autocomplete {
    position: relative;
  }

  .autocomplete-results {
    position: absolute;
    width: 100%;
    padding: 0;
    margin: 0;
    background: #fff;
    border: 1px solid #eeeeee;
    height: 120px;
    overflow: auto;
  }

  .autocomplete-result {
    list-style: none;
    text-align: left;
    padding: 4px 15px;
    cursor: pointer;
  }

  .autocomplete-result:hover {
    background-color: #fed93a;
    color: white;
  }
</style>

<script>
  export default {
    props: {
      items: {
        type: Array,
        required: false,
        default: () => [],
      },
    },
    data() {
      return {
        search: '',
        results: [],
        isOpen: false,
      };
    },
    methods: {
      onChange() {
        this.isOpen = true;
        this.filterResults();
      },
      setResult(result) {
        this.search = result;
        this.isOpen = false;
      },
      filterResults() {
        this.results = this.items.filter(item => item.toLowerCase().indexOf(this.search.toLowerCase()) > -1);
      },
    },
  }
</script>
